<template>
  <div class="ao-person-sfrz" v-show="show">
    <div class="title">{{ sfrz_txt[0] }}</div>
    <div class="line"></div>
    <el-button v-if="is_auth == 1" type="primary" style="margin-top: 16px;">{{ sfrz_txt[1] }}</el-button>
    <el-button v-else-if="is_auth == 2" type="primary" style="margin-top: 16px;">{{ sfrz_txt[2] }}</el-button>
    <el-form v-else ref="form" :model="form" label-width="100px">
      <el-form-item :label="sfrz_txt[3]">
        <el-upload
          :action="upload_img_url"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          list-type="picture-card"
          :on-preview="handlePictureCardPreview"
          :data="{key: 'id_card_face'}"
        >
          <img v-if="form.id_card_face" :src="form.id_card_face" class="avatar" />
          <i v-else class="el-icon-plus"></i>
        </el-upload>
      </el-form-item>
      <el-form-item :label="sfrz_txt[4]">
        <el-upload
          :action="upload_img_url"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          list-type="picture-card"
          :on-preview="handlePictureCardPreview"
          :data="{key: 'id_card_back'}"
        >
          <img v-if="form.id_card_back" :src="form.id_card_back" class="avatar" />
          <i v-else class="el-icon-plus"></i>
        </el-upload>
      </el-form-item>
      <el-form-item v-if="is_auth == -1">
        <div v-if="zh" style="font-size: 14px;color: #fff;">{{ zh  }}</div>
        <div v-if="en" style="font-size: 14px;color: #fff;">{{ en }}</div>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="rz">{{ sfrz_txt[5] }}</el-button>
      </el-form-item>
    </el-form>
    <el-dialog :visible.sync="dialogVisible">
      <img width="100%" :src="dialogImageUrl" alt />
    </el-dialog>
  </div>
</template>

<script>
const upload_img_url = process.env.VUE_APP_BASE_API + "web/uploadImg";
import { userIdCard, idcardinfo } from "@/api/user";

export default {
  name: "AOAssetReturn",
  data() {
    return {
      sfrz_txt: this.$t('person.sfrz_txt'),
      upload_img_url,
      form: {
        id_card_face: "",
        id_card_back: ""
      },
      dialogImageUrl: "",
      dialogVisible: false,
      is_auth: -1,
      show: false,
      zh: '',
      en: ''
    };
  },
  mounted() {
    idcardinfo().then(res => {
      this.show = true
      this.is_auth = res.is_auth
      this.zh = res.message_zh
      this.en = res.message_en
      console.log(res)
    })
  },
  methods: {
    rz() {
      userIdCard(this.form).then(res => {
        this.$store.dispatch('updateInfo', { is_auth: 2 });
      });
    },
    handleAvatarSuccess(res, file) {
      if (res.message) {
        this.$message({
          message: res.message,
          type: res.code === 0 ? "success" : "error"
        });
      }
      if (res.code === 0) {
        this.form[res.key] = res.data.url;
      }
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    }
  }
};
</script>

<style lang="scss">
.ao-person-sfrz {
  .title {
    width: 100%;
    line-height: 60px;
    background: #1a243b;
    border-radius: 4px;
    padding-left: 20px;
    color: #c7cce6;
    margin-top: -34px;
  }
  .el-form {
    margin-top: 16px;
  }
  .line {
    height: 20px;
    background: #273041;
    margin: 0 -34px;
    font-size: 18px;
  }

  .el-button {
    flex: 1;
    width: 200px;
    line-height: 46px;
    background-color: #7a98f7;
    border-radius: 4px;
    color: #fff;
    border: none;
    padding: 0;
  }
  .el-upload--picture-card {
    width: 296px;
  }
  .avatar {
    height: 146px;
    width: 296px;
    border-radius: 6px;
  }
}
</style>



